import * as ActionTypes from './ActionTypes';


export const Dishes = (state = {isLoading: true, errMess: null, dishes: []}, action) => {
	switch(action.type) {
		case ActionTypes.ADD_DISHES:
			return {...state, isLoading: false, errMess: null, dishes: action.payload}; //in ADD_DISHES payload carries the dishes

		case ActionTypes.DISHES_LOADING:
			return {...state, isLoading: true, errMess: null, dishes: []}; //in DISHES_LOADING no payload is defined

		case ActionTypes.DISHES_FAILED:
			return {...state, isLoading: false, errMess: action.payload, dishes: []}; //in DISHES_FAILED payload carries the error message

		default:
			return state;
	}
}