import React, { Component } from 'react';
import { Card, CardImg, CardText, CardBody, CardTitle, Breadcrumb, BreadcrumbItem, Button, Modal, ModalHeader, ModalBody, Row, Label} from 'reactstrap';
import { Link } from 'react-router-dom';
import { Control, LocalForm, Errors } from 'react-redux-form';
import { Loading } from './LoadingComponent';
import { baseUrl } from '../shared/baseUrl';
import { FadeTransform, Fade, Stagger } from 'react-animation-components';

const required = (val) => val && val.length;
const maxLength = (len) => (val) => !val || val.length <= len;
const minLength = (len) => (val) => val && val.length >= len;

function RenderComments({comments, postComment, dishId}) {

	if(comments == null){
		return(
			<div></div>
			);
	}


	return (
		<>
		<h4>Comments</h4>
		<ul className="list-unstyled">
		<Stagger in>
		{comments.map((comment) => {
			return (
			<Fade in>
			<li key={comment.id} className="mb-3"><p>-- {comment.comment}</p><p>{comment.author}, {new Intl.DateTimeFormat('en-US', {year: 'numeric', month: 'short', day:'2-digit'}).format(new Date(Date.parse(comment.date)))}</p></li>
			</Fade>
			);
		})}
		</Stagger>	
		</ul>
		<CommentForm dishId={dishId} postComment={postComment} />
		</>
		);
}

function RenderDish({dish}) {			

	if(dish == null){
		return(
			<div></div>
			);
	}			

	return (	
		<FadeTransform in transformProps={{
			exitTransform: 'scale(0.5) translateY(-50%)'
		}}>
		<Card>
		<CardImg width="100%" src={baseUrl + 'assets/' + dish.image} alt={dish.name} />
		<CardBody>
		<CardTitle>{dish.name}</CardTitle>
		<CardText>{dish.description}</CardText>
		</CardBody>
		</Card>
		</FadeTransform>
		);				
}


const DishDetail = (props) => {				
	if(props.isLoading) {
		return(
			<div className="container">
			<div className="row">
			<Loading />
			</div>
			</div>
			);
	}
	else if (props.errMess){
		return(
			<div className="container">
			<div className="row">
			<h4>{props.errMess}</h4>
			</div>
			</div>
			);		
	}

	else if(props.dish != null) {

		return(
			<div className="container">
			<div className="row">
			<Breadcrumb>
			<BreadcrumbItem>		
			<Link to='/menu'>Menu</Link>
			</BreadcrumbItem>
			<BreadcrumbItem active>
			{props.dish.name}
			</BreadcrumbItem>
			</Breadcrumb>
			<div className="col-12">
			<h3>{props.dish.name}</h3>
			<hr/>
			</div>
			</div>

			<div className="row">					
			<div className="col-12 col-md-5 m-1">
			<RenderDish dish={props.dish} />
			</div>
			<div className="col-12 col-md-5 m-1">
			<RenderComments comments={props.comments} 
			postComment={props.postComment}
			dishId={props.dish.id}
			/>
			</div>
			</div>
			</div>
			);					
	}
	else {
		return(
			<div></div>
			);
	}
};

class CommentForm extends Component {

	constructor(props) {
		super(props);		
		this.state = {
			isModalOpen: false
		};			
		this.toggleModal = this.toggleModal.bind(this);			
		this.handleSubmit = this.handleSubmit.bind(this);
	}


	handleSubmit(values){
		this.toggleModal();
		this.props.postComment(this.props.dishId, values.rating, values.author, values.comment);
	}

	toggleModal() {
		this.setState({isModalOpen: !this.state.isModalOpen});
	}

	render(){
		return(
			<>
			<Button outline onClick={this.toggleModal}>
			<span className="fa fa-pencil fa-lg"> Submit Comment</span>
			</Button>		

			<Modal isOpen={this.state.isModalOpen} toggle={this.toggleModal} >
			<ModalHeader toggle={this.toggleModal} >Submit Comment</ModalHeader>
			<ModalBody>
			<div className="col-12 col-md-9">
			<LocalForm onSubmit={(values) => this.handleSubmit(values)}>

			<Row className="form-group">
			<Label htmlFor="rating">Rating</Label>			
			<Control.select model=".rating" name="rating" className="form-control">
			<option>1</option>
			<option>2</option>
			<option>3</option>
			<option>4</option>
			<option>5</option>
			</Control.select>
			</Row>

			<Row className="form-group">
			<Label htmlFor="name">Your Name</Label>			
			<Control.text model=".author" id="author" name="author" placeholder="Your Name" className="form-control" validators={{required, minLength: minLength(3), maxLength: maxLength(15)}} />    
			<Errors className="text-danger" model=".author" show="touched" messages={{required: 'Required', minLength: 'Must be greater than 2 charachters', maxLength: 'Must be 15 charachters or less'}}/>
			</Row>

			<Row className="form-group">
			<Label htmlFor="comment">Comment</Label>			
			<Control.textarea model=".comment" id="comment" name="comment" className="form-control" rows="6" />    
			</Row>

			<Row>
			<Button type="submit" color="primary">
			Submit
			</Button>
			</Row>
			</LocalForm>
			</div>
			</ModalBody>
			</Modal>
			</>
			);
	}

}


export default DishDetail;